import React, { Component, Fragment } from 'react';
import { Provider } from 'react-redux';
import { BrowserRouter, Route , Switch } from 'react-router-dom';
import Header from './components/Header/index';
import Footer from './components/Footer/index';
import Login from './components/Login/index';
import Book from './pages/BookPage/index';
import store from './store';
import IndexBook from './pages/IndexBookPage/index';
import Person from './pages/PersonPage/index';
import ManagePage from './pages/ManagePages/index';
import BookList from './pages/SearchBookList/index';
// import UpdateInfo from './pages/PersonPage/components/UpdataInfo/index';
// import MyOrder from './pages/PersonPage/components/MyOrder/index';
// import ManagerBook from './pages/ManagePages/managerBook';
// import ManagerUser from './pages/ManagePages/managerUser';
// import ManagerOrder from './pages/ManagePages/managerOrder';
class App extends Component {

  render() {
    return (
    	<Provider store={store}>
      	<BrowserRouter>
      		<div>
            <Header/>
      			<Switch>
              <Route path="/index" component={IndexBook}></Route>
              <Route path="/login" component={Login}></Route>
              <Route path="/book" component={Book}></Route>
              <Route path="/person" component={Person}></Route>
              <Route path="/manager" component={ManagePage}></Route>
              <Route path="/booklist" component={BookList}></Route>
            </Switch>
            <Footer/>
      		</div>
      	</BrowserRouter>
      </Provider>
    );
  }
}

export default App;
