import axios from 'axios';
import * as constants from './constants';

const changeComment = (result) => ({
	type: constants.CHANGECOMMENT,
	value: result.comment
})

export const getComment = (bookid) => {
	return (dispatch) => {
		axios.get('/bookComment?bookid=' + bookid).then((res) => {

			const result = res.data.data;
			if (result) {
				dispatch(changeComment(result));
			
			}else {
				alert('登陆失败')
			}
		})
	}
}