/* eslint-disable no-useless-constructor */
import React,{Component, Fragment} from 'react';
import {connect} from 'react-redux'
import AlertWrapper from '../../../../components/alertItem/HighalertWrapper';
import AddBookHide from './AddBookHide';

import { actionCreators } from '../../store/index';
import {
    FormTitle,
    ItemNew,
    More


}from './style'
class AddBook extends Component{
    constructor(){
        super();
    }
   
    render(){
            const {showItem,changeHide,showAddBook}=this.props;
  
        return(
         
                <Fragment >
                     <FormTitle className="formTitle">新建书籍</FormTitle> 
                    <form>
                         <ItemNew className="itemNew">        
                            <label>*书籍名称：</label><input type="number" required></input>
                         </ItemNew>
                         <ItemNew className="itemNew">
                            <label>*作者：</label><input type="text" required></input>
                         </ItemNew>                         
                         <ItemNew className="itemNew">
                            <label>*出版社：</label><input type="text" placeholder="出版社" required></input>
                         </ItemNew>    
                         <ItemNew className="itemNew ">
                            <label>*价格</label><input type="number" placeholder="价格" required></input>
                         </ItemNew>  
                         <ItemNew className="itemNew" >
                            <label>*是否上架</label>
                            <input type="radio" value="1" className="inputRadio"/>是
                            <input type="radio" value="0" className="inputRadio" />否

                        </ItemNew>
                         <ItemNew className="itemNew " >
                            <label>*库存</label>
                            <input type="number" placeholder="库存" required></input>
                        </ItemNew>
                         <ItemNew className="itemNew detail" >
                            <label>*详细说明</label>
                            <textarea type="text" required ></textarea>
                        </ItemNew>
                        
                         {
                             showItem?<AddBookHide/>:(  <More className="more" onClick={changeHide}>
                                                                <span>更多</span>  
                                                                <i className="iconfont jiantou">&#xe646;</i>
                                                         </More>  
                                                      )
                         }  
                         <button className="comfirm" onClick={showAddBook} onSubmit={this.props.submitBook}>确定</button>  
                         <span className="cancel" onClick={showAddBook}>取消</span>
                    </form>
                </Fragment>
            
        )
    }
}
const mapStateToProps=(state)=>({
    showItem:state.getIn(['manage','showItem'])

})
const mapDispatchToProps=(dispatch)=>({
      changeHide(){
          dispatch(actionCreators.changeHide());
      },
      showAddBook(){
          dispatch(actionCreators.showNew());
      },
      submitBook(){
          
      }

})

AddBook=connect(mapStateToProps,mapDispatchToProps)(AddBook);
export default AlertWrapper(AddBook);