import axios from 'axios';
import * as constants from './constants';
const changeSearchBookList=(data)=>({
	type:constants.GET_SEARCH_BOOK_LIST,
	value:data
})
export const searchByBookNameOrTypes=(name)=>{
	return (dispatch)=>{
		axios({
			url:"/searchByBook",
			data:name,
			method:'post'
		}).then(function (response) {
			const result=response.data.success;
			const data = response.data.data;
			if (result) {
				dispatch(changeSearchBookList(data));
			
			}else {
				console.log('未找到匹配项')
			}
		})
	}
}
const changeLogin = (data) => ({
	type: constants.CHANGE_LOGIN,
	value: true,
	userInfo:data
})

export const logout = () => ({
	type: constants.LOGOUT,
	value: false
})

export const login = (phone, password) => {
	return (dispatch) => {
		axios.get('./api/login.json?phone=' + phone + '&password=' + password).then((res) => {
			console.log("login ");
			
			const result = res.data.success;
			const data=res.data.data;
			if (result) {
				dispatch(changeLogin(data));
			
			}else {
				alert('登陆失败')
			}
		})
	}
}