import React,{Component} from 'react';
import {
    PersonInfoWrapper,
    NavBar,
    NavHeader
}from '../style'
class PersonInfo extends  Component{
    render(){
        const { user }=this.props;
        return(
       
                <PersonInfoWrapper class="personInfo">
                    <NavBar class="navbar navbar-default" role="navigation">
                            <div class="container-fluid">
                            <NavHeader class="navbar-header">
                                <img className="phone" src="http://img7x1.ddimg.cn/imghead/15/9/9921910134921-1_o.png?=5792"/>
                                {/* style="color:black;margin-left:10px;" */}
                                <span className="userName">{user.phone}</span>
                            </NavHeader>
                        
                            </div>
                        
                    </NavBar>
                    <ul class="breadcrumb order">
                            <li class="first"><a href="#">待付款</a></li>
                            <li><a href="#">待发货</a></li>
                            <li><a href="#">待收货</a></li>
                            <li><a>待评价</a></li>
                            <li><a>退款</a></li>
                    </ul>
                </PersonInfoWrapper>
      
        )
    }
}
export default PersonInfo;