import React,{Component, Fragment}from 'react';
import { Redirect , Link } from 'react-router-dom';
import { connect } from 'react-redux';
import { actionCreators } from '../store';
import Button from '../Button/index';
import {
    HeaderWrapper,
    Nav,
    NavBar,
    SearchBook
}from './style';
import axios from 'axios';
class Header extends Component{
    
    constructor(props){
        super(props);
        this.state={
            searchName:""
        }
        this.handleChangeInput=this.handleChangeInput.bind(this)
    }
    handleChangeInput(e){
        this.setState({
            searchName:e.target.value
        })

    }
    render(){
       
        const {loginState,loginOut,userInfo}=this.props;
        console.log(this.state.searchName)
        return(
            <HeaderWrapper>
                <Nav>
                      
                        <NavBar>
                            <Link className="brand" to="/index">网上书店</Link>
                            
                            
                        </NavBar>   
                                  
                        <div>
                            {
                                !loginState?(
                                    <Fragment>
                                        <SearchBook>
                                                <input type="text" className="search" 
                                                        value={this.state.searchName} onChange={this.handleChangeInput}/>
                                                <Button className="primary" onClick={()=>this.props.searchByBookNameOrTypes(this.state.searchName)}>搜索</Button>
                                        </SearchBook>  
                                        <div className="button">
                                        <Button  className="default" href="login.jsp">注册</Button>
                                        <Link to="/login">
                                                <Button className="primary" href="register.jsp">登录</Button>
                                        </Link>
                                        
                                        </div>
                                    </Fragment>
                                    
                                ):(
                                    <div> 
                                        <Button className="default four" type="button" onClick={loginOut}>退出登录</Button>
                                        <Link to={{pathname:"/person",
                                                   state:JSON.stringify(userInfo)}}>
                                            <Button  className="personal primary">hi,{userInfo.get("phone")}</Button>
                                        </Link>
                                        <Link to="/manager/managerBook">
                                            <Button className="success four" href="AllBook">管理书籍  </Button>
                                        </Link>
                                        <Link to="/manager/managerOrder">
                                            <Button  className="info four" href="ShowOrder">  管理订单</Button>
                                        </Link>
                                        
                                        <Link to="/manager/managerUser">
                                            <Button  className="warning four" href="AllUser"> 管理用户 </Button>
                                        </Link>
                                       
                                        
                                       
                                    </div>
                                    
                                )
                            }
                            
                            
                        </div> 

                </Nav>
            </HeaderWrapper>
        )
    }
}
const mapState = (state) => ({
	loginState: state.getIn(['common', 'login']),
    userInfo:state.getIn(["common","userInfo"]),
    searchBookByNameList:state.getIn(["common","searchBookByNameList"])
})

const mapDispatch = (dispatch) => ({
	login(accountElem, passwordElem){
		dispatch(actionCreators.login(accountElem.value, passwordElem.value))
	},
    loginOut(){
        dispatch(actionCreators.logout());
    },
    searchByBookNameOrTypes(name){
        dispatch(actionCreators.searchByBookNameOrTypes(name))
    }
    
})

export default connect(mapState, mapDispatch)(Header);