import React,{Component} from 'react';
// import PersonInfo from './components/PersonInfo';
// import RecentOrder from './components/RecentOrder';
// import GusLike from './components/GusLike';
import LeftSide from './components/LeftSide';
import MyOrder from './components/MyOrder/index';
import UpdateInfo from './components/UpdataInfo/index';
import {  Route , Switch } from 'react-router-dom';
import PersonIndex from './components/PersonIndex'
import { connect } from 'react-redux';
import { actionCreators } from './store/index';
import {
    PersonWrapper,
    // MainSection
    
}from './style'
class Person extends  Component{
    constructor(props){
        super(props);
        let userInfo=this.props.location.state;
        userInfo=JSON.parse(userInfo)
        this.state={
            userInfo:userInfo
            
        }
    }
    componentDidMount(){
        this.props.getPersonList(this.state.userInfo.id)
    }
    render(){
        const {user}=this.props;
        return(
            <PersonWrapper>
                <LeftSide user={user}/>
            
                    <Switch>
                        <Route path="/person" exact component={PersonIndex}></Route>
                        <Route path="/person/updateInfo" exact component={UpdateInfo}></Route>
                        <Route path="/person/myOrder" exact component={MyOrder}></Route>
                    </Switch>
             
           
               
            </PersonWrapper>
        )
    }
}
const mapState = (state) => ({
	user: state.getIn(['person', 'user']),
})

const mapDispatch = (dispatch) => ({
	getPersonList(id){
		dispatch(actionCreators.getPersonList(id));
	}
})
export default connect(mapState,mapDispatch)(Person);