import axios from 'axios';
import * as constants from './constants';

const changeRecentOrder = (data) => ({
	type: constants.CHANGERECENTORDER,
	RecentOrder: data
})

const changePersonList=(data)=>({
	type:constants.GET_PERSONDATA,
	user:data.user,
	usergusLike:data.usergusLike,
	userrecentOrder: data.userrecentOrder
})
const changePerson=(data)=>({
	type:constants.CHANGE_PERSON,
	user:data.user

})
