export const updatePersonInfo=(user)=>{
	return (dispatch)=>{
		axios.get('/update?user='+user).then(function (response) {
			const data=response.data.data
			const result = response.data.success;
			if (result) {
				dispatch(changePerson(data));
			}else {
				console.log('获取失败')
			}
		})
	}
}
export const getPersonList=()=>{
	return (dispatch)=>{
		axios.get('/personList').then(function(response){
			const data=response.data.data
			const result = response.data.success;
			if (result) {
				dispatch(changePersonList(data));
			}else {
				console.log('获取失败')
			}
		})
	}
}
