import React,{Component} from 'react';

import {
    CarouselWrapper,
    CarouselBox,
    ImgItem,
    CarouselDots
}from '../style'
class Carousel extends Component {
    
    render(){
        const {imgList}=this.props;
        return(
            <CarouselWrapper>
                <CarouselBox>
                    <i className="iconfont left" >&#xe61c;</i>
                    {
                        imgList.map((item) =>{
                            return (
                                <ImgItem key={item.get("id")}>
                                   
                                    <img 
                                        className="banner" 
                                        src={item.get('imgUrl')}
                                        alt=''/>
                                  
                                </ImgItem>
                            )
                           
                        })
                    }
                    <i className="iconfont right">&#xe7a7;</i>
                  <CarouselDots>
                      <div></div>
                      <div></div>
                      <div></div>
                  </CarouselDots>
                </CarouselBox>


            </CarouselWrapper>
        )
    }
}



export default Carousel;