import React,{ Component } from 'react';
import LeftSide from '../LeftSide';
import { Link } from 'react-router-dom';
import Button from '../../../../components/Button/index';
import {
    MyOrderWrapper,
    MainSection,
    NavOrder,
    TitleOrder,
    TitleSheet,
    OrderItemTitle,
    MyOrderItem,
    OrderItemMain
} from './style'
class MyOrder extends Component{
    render(){
        const order={};
        return(
            <MyOrderWrapper>

                <MainSection>
                    <NavOrder className="navOrder">
                        <TitleOrder className="titleOrd">全部订单</TitleOrder>
                        
                        <TitleSheet>
                            <span>书籍</span>
                            <span className="price">单价</span>
                            <span className="number">数量</span>
                            <span className="state">交易状态</span>
                        
                        </TitleSheet> 
                    </NavOrder>

                    <MyOrderItem  >
                        <OrderItemTitle className="secTitle">
                            <span className="date">时间:${order.date }</span>
                            <span className="orderNumber">订单号：${order.oid }</span>

                        </OrderItemTitle>
                        <OrderItemMain class="secMain">
                        {/* style="width:80px;height:80px;" */}
                            <img src="${order.goodPic }" />
                            <span className="name">${order.goodName }</span>
                            <span className="price">￥${order.goodPrice }</span>
                            <span className="address">${order.detailAddress }</span>
                            <Link to={{
                                    pathname:"/book/comment",
                                    state:JSON.stringify(order) 
                            }} class="btn btn-info assess">
                                <Button className="primary">评价</Button></Link>
                        </OrderItemMain>
                </MyOrderItem>

	

                </MainSection>

            </MyOrderWrapper>
        )
    }
}
export default MyOrder;