import styled from 'styled-components';
export const HeaderWrapper=styled.div`
    position:relative;
    a{
        text-decoration:none;
        color:rgb(119, 119, 119);
        font-size:14px;
        line-height:32px;
    }
    .brand{
        font-size:18px;
    }
    .button{
        //  float:left;
    }
`
export const Nav=styled.div`
    padding:8px;
    font-size:14px;
    background-color:rgb(248, 248, 248);
    height:40px;
    width:98.7%;
    border-bottom:1px solid  rgb(231, 231, 231);
    &.footBar{
        // position:absolute;
        position:relative;
        // margin-top:-20px;
        bottom:0;
        height:40px;
        color:rgb(51, 51, 51);
        font-size:14px;
        p{
            
            line-height:50px;
            text-align:center;
        }
     
    }

`
export const NavBar=styled.div`
    width:200px;
    float:left;
    
`;
export const SearchBook=styled.div`
    float:left;
    position:absolute;
    left:30%;
    margin-top:2px;
    input{
        border:1px solid rgb(204, 204, 204);
        border-radius:4px;
        box-shadow:rgba(0, 0, 0, 0.075) 0px 1px 1px 0px inset;
        color:rgb(85, 85, 85);
        height:30px;
        line-height:20px;
        width:200px;
        margin-right:10px;
    }

`
