/* eslint-disable no-useless-constructor */
import React,{Component, Fragment} from 'react';
import {connect} from 'react-redux'
import AlertWrapper from '../../../../components/alertItem/HighalertWrapper';
import AddBookHide from './AddBookHide';

import { actionCreators } from '../../store/index';
import {
    FormTitle,
    ItemNew,
    More


}from './style'
class UpdateBook extends Component{
    constructor(){
        super();
    }
   
    render(){
            const {showItem,changeHide,showUpdateBook ,book}=this.props;
            console.log(book.name)
        return(
         
                <Fragment >
                     <FormTitle className="formTitle">更新书籍</FormTitle> 
                    <form>
                         <ItemNew className="itemNew">        
                            <label>*书籍名称：</label>
                            <input type="text" value={book.name} ></input>
                         </ItemNew>
                         <ItemNew className="itemNew">
                            <label>*作者：</label><input type="text" value={book.author}></input>
                         </ItemNew>                         
                         <ItemNew className="itemNew">
                            <label>*出版社：</label><input type="text" value={book.publisher}></input>
                         </ItemNew>    
                         <ItemNew className="itemNew ">
                            <label>*价格</label><input type="number" value={book.price} ></input>
                         </ItemNew>  
                         <ItemNew className="itemNew" >
                            <label>*是否上架</label>
                            <input type="radio" value="1" className="inputRadio"/>是
                            <input type="radio" value="0" className="inputRadio" />否

                        </ItemNew>
                         <ItemNew className="itemNew " >
                            <label>*库存</label>
                            <input type="number" value={book.stock}></input>
                        </ItemNew>
                         <ItemNew className="itemNew detail" >
                            <label>*详细说明</label>
                            <textarea type="text" value={book.detail}></textarea>
                        </ItemNew>
                        
                         {
                             showItem?<AddBookHide/>:(  <More className="more" onClick={changeHide}>
                                                                <span>更多</span>  
                                                                <i className="iconfont jiantou">&#xe646;</i>
                                                         </More>  
                                                      )
                         }  
                         <button className="comfirm"  onSubmit={this.props.updateBook}>确定</button>  
                         <span className="cancel" onClick={showUpdateBook}>取消</span>
                    </form>
                </Fragment>
            
        )
    }
}
const mapStateToProps=(state)=>({
    showItem:state.getIn(['manage','showItem'])

})
const mapDispatchToProps=(dispatch)=>({
      changeHide(){
          dispatch(actionCreators.changeHide());
      },
      showUpdateBook(){
          dispatch(actionCreators.showUpdate());
      },
      updateBook(book){
          dispatch(actionCreators.updateBook(book))
      }
     

})

UpdateBook=connect(mapStateToProps,mapDispatchToProps)(UpdateBook);
export default AlertWrapper(UpdateBook);